
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import { isIOS, isIphoneX } from '@/assets/js/brower'
import answerImage from '@/assets/img/answer.png'
import BottomInput from '@/components/bottom-input/bottom-input'
import dingImage from '@/assets/img/ding.png'
import myReplyImage from '@/assets/img/my_reply.png'
import Rater from 'vux/src/components/rater/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
data () {
return {
starNum: 5,
answerImage: answerImage,
dingImage: dingImage,
scrollHeight: 0,
showInput: false,
placeholder: '回复 XX 的评论',
myReplyImage: myReplyImage
}
},
components: {
VHeader, Scroll, isIOS, isIphoneX, Rater, Cell, BottomInput
},
mounted () {

this.scrollHeight = window.innerHeight - 60
},
created () {






},
methods: {
handleGoReport () {
this.$router.push('/user/report')
},
hangdleGoQuestionAndAnswer () {
this.$router.push('/user/questionandanswer')
},
handleShowInput () {
this.showInput = true
}
},
watch: {}
}



